}

@font-face {
  font-family: "Raleway";
  font-display: swap;
  src: url("../fonts/Raleway-VariableFont_wght.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Raleway";
  font-display: swap;
  src: url("../fonts/Raleway-Bold-VariableFont_wght.woff") format("woff");
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/Rubik-Bold-VariableFont_wght.woff") format("woff");
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/Rubik-VariableFont_wght.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

.wrapper {
  overflow: hidden;
  min-height: 100%;
}

.container {
  max-width: 1190px;
  padding: 0 15px;
  margin: 0 auto;
}

.btn {
  color: #fff;
  display: inline-block;
  padding: 15px 30px;
  font-family: "Raleway";
  font-weight: 800;
  border-radius: 8px;
  min-width: 150px;
  text-align: center;
}

.btn-red {
  background-color: #ec5863;
}

.btn-blue {
  background-color: #4285f4;
}

.page {
  position: relative;
  z-index: 1;
  background-color: #fafbfd;
}

.page::before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 300px;
  height: 100%;
  z-index: -1;
  border-radius: 25px 0 0 25px;
  background: url("../images/page/back-group/01.png") 0 0/100% 100% no-repeat;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}

.header.fixed {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background-color: #fff;
  z-index: 99;
  -webkit-box-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
          box-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
}

.header__body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  height: 85px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.header__logo {
  font-size: 20px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #000;
}

.header__logo::before {
  margin-right: 10px;
  font-size: 40px;
}

.header__logo span {
  font-family: "Rubik";
  font-weight: 700;
}

.header__logo span span {
  color: #ec5863;
}

.hamburger-menu {
  display: none;
}

.nav__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.nav__list li {
  margin: 0 20px;
}

.nav__link {
  font-size: calc(20px + -5 * ((100vw - 320px) / 870));
  font-family: "Raleway";
  font-weight: 700;
  text-transform: uppercase;
  color: #000;
  padding-bottom: 6px;
  -webkit-transition: border 0.2s linear;
  -o-transition: border 0.2s linear;
  transition: border 0.2s linear;
  border-bottom: 2px solid transparent;
}

.nav__link:hover,
.nav__link.active {
  border-color: #000;
}

.main-block {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.main-block_support {
  text-align: center;
  display: block;
  max-width: 800px;
  margin: 0 auto;
}

.main-block_support .main-block__text {
  padding: 0 30px;
}

.main-block__content {
  -webkit-box-flex: 0;
      -ms-flex: 0 1 400px;
          flex: 0 1 400px;
  padding: 0 10px;
}

.main-block__content_feature {
  margin: 0 auto;
}

.main-block__image img:not(.back-group__feature) {
  max-width: 100%;
  display: block;
  height: auto;
}

.main-block__image_feature {
  padding: 40px 20px 20px 50px;
}

.main-block__image_info {
  cursor: pointer;
  position: relative;
  padding: 20px 90px;
  background-color: #fff;
  border-radius: 15px;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.main-block__image_info::before {
  content: "";
  display: block;
  width: 45px;
  height: 45px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: url("../images/info/arrow.png");
}

.main-block__title {
  font-size: calc(30px + 10 * ((100vw - 320px) / 870));
  font-family: "Rubik";
  font-weight: 700;
}

.main-block__title.blue span {
  color: #4285f4;
}

.main-block__title.red span {
  color: #ec5863;
}

.main-block__text {
  margin-top: 20px;
  font-family: "Rubik";
  font-weight: 400;
  color: #616161;
  font-size: 15px;
}

.main-block__btn {
  margin-top: 20px;
}

.back-group {
  position: relative;
}

.back-group__feature {
  position: absolute;
  z-index: -1;
}

.back-group__feature_1 {
  left: 0;
  top: 0;
}

.back-group__feature_2 {
  left: 50px;
  bottom: 30px;
}

.back-group__feature_3 {
  right: 30px;
  bottom: 200px;
}

.back-group__info {
  position: absolute;
  z-index: -1;
}

.back-group__info_1 {
  left: -30px;
  bottom: -30px;
}

.back-group__info_2 {
  top: -40px;
  left: 200px;
}

.back-group__info_3 {
  top: -25px;
  right: -60px;
}

.back-group__support {
  position: absolute;
  z-index: -1;
}

.back-group__support_1 {
  right: 28%;
  bottom: 0;
}

.back-group__support_2 {
  right: 0;
  top: -30px;
}

.back-group__support_3 {
  left: 27%;
  top: 50px;
}

.back-group__support_4 {
  left: 0;
  bottom: 10px;
}

.back-group__feature,
.back-group__info,
.back-group__support {
  -webkit-animation: circle 20s infinite linear;
          animation: circle 20s infinite linear;
}

@-webkit-keyframes circle {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes circle {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.intro {
  padding: 160px 0;
}

.intro__play-video {
  font-family: "Rubik";
  margin-top: 120px;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 100%;
          flex: 1 1 100%;
  position: relative;
  padding-left: 70px;
}

.intro__play-video::before {
  cursor: pointer;
  content: "";
  border: 1px solid #4285f4;
  position: absolute;
  left: 0;
  top: -10px;
  width: 55px;
  height: 55px;
  display: block;
  z-index: 1;
  border-radius: 50%;
}

.intro__play-video::after {
  content: "";
  position: absolute;
  left: 23.5px;
  top: 9.5px;
  width: 14px;
  height: 14px;
  display: block;
  background: url("../images/intro/arrow.png") no-repeat;
}

.intro__title {
  font-size: 13px;
  letter-spacing: 2px;
  color: #b0b0b0;
}

.intro__sup-title {
  margin-top: 8px;
  text-transform: uppercase;
}

.feature {
  padding: 80px 0;
  position: relative;
  z-index: 0;
}

.info {
  background-color: #fafbfd;
  padding: 60px 0 120px;
  position: relative;
  z-index: 0;
}

.info__counter {
  margin-bottom: 55px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.info__die {
  background-color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 -40px;
  padding: 20px 40px;
  border-radius: 15px;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.info__item {
  text-align: center;
  padding: 0 40px;
  -webkit-box-flex: 0;
      -ms-flex: 0 1 33.333%;
          flex: 0 1 33.333%;
  font-family: "Rubik";
}

.info__num {
  color: #ec5863;
  font-size: 36px;
}

.info__name {
  margin-top: 10px;
  text-transform: uppercase;
  font-size: 15px;
}

.support {
  position: relative;
  z-index: 0;
}

.support__body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 60px -35px 30px;
  padding-bottom: 15px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.support__col {
  -webkit-box-flex: 0;
      -ms-flex: 0 1 33.333%;
          flex: 0 1 33.333%;
  padding: 0 35px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 30px;
}

.support__item {
  cursor: pointer;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  text-align: center;
  border-radius: 15px;
  padding: 36px 18px;
  background-color: #fff;
  font-family: "Rubik";
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.support__icon i {
  font-size: 66px;
}

.support__name {
  margin-top: 60px;
  font-size: 16px;
}

.support__text {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  font-size: 15px;
  margin-top: 20px;
  color: #616161;
}

.support__download {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.support__download a {
  margin: 0 27.5px;
}

.support__download a img {
  max-width: 100%;
}

.footer {
  padding: 80px 0 40px;
}

@media (max-width: 767px) {
  .page::before {
    display: none;
  }

  .header__logo {
    -webkit-box-ordinal-group: 2;
        -ms-flex-order: 1;
            order: 1;
    z-index: 2;
  }

  .header__hamburger-menu {
    -webkit-box-ordinal-group: 4;
        -ms-flex-order: 3;
            order: 3;
    z-index: 1;
  }

  .header__download {
    -webkit-box-ordinal-group: 3;
        -ms-flex-order: 2;
            order: 2;
    z-index: 2;
  }

  .header__nav {
    -webkit-box-ordinal-group: 5;
        -ms-flex-order: 4;
            order: 4;
  }

  .hamburger-menu {
    display: block;
    height: 25px;
    cursor: pointer;
    position: relative;
    width: 35px;
  }

  .hamburger-menu::before,
  .hamburger-menu span,
  .hamburger-menu::after {
    background-color: #000;
    height: 2px;
    width: 100%;
    position: absolute;
    -webkit-transition: all 0.4s linear;
    -o-transition: all 0.4s linear;
    transition: all 0.4s linear;
    left: 0;
  }

  .hamburger-menu::before {
    content: "";
    top: 0;
  }

  .hamburger-menu span {
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
  }

  .hamburger-menu::after {
    content: "";
    bottom: 0;
  }

  .hamburger-menu.active::before {
    top: 50%;
    -webkit-transform: translateY(-50%) rotate(45deg);
        -ms-transform: translateY(-50%) rotate(45deg);
            transform: translateY(-50%) rotate(45deg);
  }

  .hamburger-menu.active span {
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
  }

  .hamburger-menu.active::after {
    bottom: 50%;
    -webkit-transform: translateY(50%) rotate(-45deg);
        -ms-transform: translateY(50%) rotate(-45deg);
            transform: translateY(50%) rotate(-45deg);
  }

  .nav {
    position: fixed;
    left: 0;
    top: -150%;
    overflow: auto;
    padding: 20px 15px 30px;
    margin-top: 85px;
    width: 100%;
    height: 100%;
    -webkit-transition: top 0.2s linear;
    -o-transition: top 0.2s linear;
    transition: top 0.2s linear;
    background-color: rgba(46, 44, 44, 0.918);
  }

  .nav.show {
    top: 0;
  }

  .nav__list {
    display: block;
    max-width: 500px;
    margin: 0 auto;
  }

  .nav__list li {
    margin: 0px 0px 50px 0px;
  }

  .nav__link {
    color: #fff;
  }

  .intro {
    padding: 100px 0 60px;
  }

  .intro__play-video {
    margin-top: 60px;
  }

  .intro__play-video {
    margin-top: 40px;
  }

  .feature {
    padding: 60px 0 40px;
  }

  .info {
    padding: 50px 0;
  }

  .support__body {
    margin: 60px -20px 30px;
  }

  .support__col {
    padding: 0 20px;
  }

  .support__download a {
    margin: 0 10px;
  }

  .footer {
    padding: 50px 0;
  }
}

@media (max-width: 767px) and (max-width: 425px) {
  .nav {
    margin-top: 60px;
  }
}

@media (max-width: 667px) {
  .back-group__info_1 {
    right: 0;
